body {
  font-family: BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", Helvetica,
    Arial, sans-serif;
  margin-top: 100px;
  font-size: 16px;
  font-weight: 300;
  color: #333;
  background: #e9ebee;
}

@font-face {
  font-family: "icomoon";
  src: url("fonts/icomoon.eot?lqo9i7");
  src: url("fonts/icomoon.eot?lqo9i7#iefix") format("embedded-opentype"),
    url("fonts/icomoon.ttf?lqo9i7") format("truetype"),
    url("fonts/icomoon.woff?lqo9i7") format("woff"),
    url("fonts/icomoon.svg?lqo9i7#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-pushpin:before {
  content: "\e946";
}
.icon-facebook:before {
  content: "\ea90";
}
.icon-youtube:before {
  content: "\ea9d";
}
.icon-github:before {
  content: "\eab0";
}
.icon-linkedin2:before {
  content: "\eaca";
}

a {
  color: #000;
}

/* header start ------------------------------------------- */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #262626;
  z-index: 10000;
  height: 100px;
  overflow: hidden;
  -webkit-transition: height 0.3s;
  -moz-transition: height 0.3s;
  transition: height 0.3s;
}

.header h1 a {
  color: #fff;
  text-decoration: none;
}

.header h1,
.header nav a {
  line-height: 100px;
}

.header h1 {
  letter-spacing: 4px;
  font-size: 2.5rem;
  margin: 0;
  float: left;
}

.header nav {
  float: right;
}

.header nav a {
  color: #aaa;
  text-decoration: none;
  font-weight: 700;
  margin: 0 0 0 20px;
  font-size: 1rem;
}

.header nav a:hover {
  color: #fff;
}

/* Transitions and class for reduced height */
.header h1,
.header nav a {
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}

.header.header-shrink {
  height: 70px;
}

.header.header-shrink h1,
.header.header-shrink nav a {
  line-height: 70px;
}

.header.header-shrink h1 {
  font-size: 1.5rem;
}

/* Example Media Queries */
@media screen and (max-width: 55em) {
  .header .inner {
    width: 100%;
  }

  .header h1,
  .header nav {
    display: block;
    margin: 0 auto;
    text-align: center;
    float: none;
  }

  .header h1,
  .header nav a {
    line-height: 115px;
  }

  .header nav a {
    margin: 0 10px;
  }

  .header.header-shrink h1,
  .header.header-shrink nav a {
    line-height: 45px;
  }

  .header.header-shrink h1 {
    font-size: 1.25em;
  }

  .header.header-shrink nav a {
    font-size: 1em;
  }
}

@media screen and (max-width: 32.25em) {
  .header nav a {
    font-size: 1em;
  }
}

@media screen and (max-width: 24em) {
  .header nav a,
  .header.header-shrink nav a {
    line-height: 1;
  }
}

/* end of header ------------------------------------------- */

section.featured-posts {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

@media only screen and (max-width: 48rem) {
  section.featured-posts {
    padding-top: 1.5rem;
  }
}

.post {
  box-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

a.post-image img {
  width: 100%;
  height: auto;
  vertical-align: middle;
}

.post-body {
  background: #fff;
}

.post .post-body {
  padding: 1.5rem;
}

.post .post-meta {
  margin-bottom: 1rem;
}

.post-title a {
  text-decoration: none;
}

.post-meta .post-category {
  font-size: 13px;
  text-transform: uppercase;
  padding: 3px 10px;
  font-weight: 600;
  margin-right: 15px;
  color: #fff;
  background-color: #212631;
  -webkit-transition: 0.2s opacity;
  transition: 0.2s opacity;
  text-decoration: none;
}

.read-more {
  font-weight: 500;
}

.single-post {
  padding: 2rem;
  background-color: #fff;
}

.single-post .post-content {
  margin-top: 3.5rem;
}

.single-post-page {
  padding-top: 4rem;
  padding-bottom: 4rem;
}

footer {
  padding: 3rem;
  text-align: center;
}

footer .logo {
  position: relative;
}

footer .logo::before {
  content: "";
  width: calc(33.333333% - 40px);
  height: 1px;
  background: #000000;
  position: absolute;
  display: block;
  top: 15px;
  left: 0;
}

footer .logo::after {
  content: "";
  width: calc(33.333333% - 40px);
  height: 1px;
  background: #000000;
  position: absolute;
  display: block;
  top: 15px;
  right: 0;
}

footer ul.social-media-icons {
  margin: 0;
  padding: 0;
  list-style: none;
  display: table;
  margin: 0 auto;
}

footer ul.social-media-icons li {
  float: left;
  margin-left: 4px;
  margin-right: 4px;
}

footer ul.social-media-icons li a {
  padding: 5px;
  height: 1rem;
  width: 1rem;
  background-color: #868686;
  border-radius: 50%;
  text-decoration: none;
  color: #fff;
  display: inline-block;
  transition: background-color 0.3s;
}

footer ul.social-media-icons li a:hover {
  background-color: #333;
}

footer .copyright {
  margin-top: 2rem;
}
/* ----------------------------------------------- */

.sidebar-widget {
  background: #fff;
  box-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
}

.widget-body {
  margin-top: 2rem;
}

.category-widget ul,
.menubar-widget ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.category-widget ul li,
.menubar-widget ul li {
  display: block;
  padding-bottom: 10px;
  border-bottom: 1px solid #fceef2;
}

.menubar-widget ul li {
  border-bottom: none;
}

.category-widget ul li:last-child,
.menubar-widget ul li:last-child {
  border-color: transparent;
}

.category-widget ul li a,
.menubar-widget ul li a {
  text-decoration: none;
  font-weight: 400;
  display: block;
  padding: 5px 0;
}

.menubar-widget ul li:last-child a {
  padding-bottom: 0;
}

.category-widget ul li > a > span {
  float: right;
  color: #fff;
  background-color: #212631;
  padding: 0 5px;
  font-weight: 600;
}

.menubar-widget ul li ul {
  padding-left: 2rem;
}

@media only screen and (max-width: 48rem) {
  .post {
    margin-bottom: 1.5rem;
  }
}

.blog-card {
  display: flex;
  flex-direction: column;
  box-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.1);
  margin-bottom: 1rem;
  background: #fff;
  line-height: 1.4;
  overflow: hidden;
  z-index: 0;
}

.blog-card .meta {
  position: relative;
  z-index: 0;
  height: 200px;
}
.blog-card .photo {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  background-position: center;
}

.blog-card .description {
  padding: 1rem;
  background: #fff;
  position: relative;
  z-index: 1;
}

.blog-card .description h3 {
  line-height: 1;
  margin-top: 1rem;
}

.blog-card .description a {
  text-decoration: none;
}

.blog-card .description .read-more a {
  display: inline-block;
  position: relative;
  color: #212631;
}

.blog-card .description .read-more a:hover {
  color: #000;
}

.blog-card p {
  position: relative;
  margin: 1rem 0 0;
}
.blog-card p:first-of-type {
  margin-top: 1.25rem;
}

.blog-card:hover .details {
  left: 0%;
}
@media (min-width: 640px) {
  .blog-card {
    flex-direction: row;
  }
  .blog-card .meta {
    flex-basis: 40%;
    height: auto;
  }
  .blog-card .description {
    flex-basis: 60%;
  }
  .blog-card .description:before {
    content: "";
    background: #fff;
    width: 30px;
    position: absolute;
    left: -10px;
    top: 0;
    bottom: 0;
    z-index: -1;
  }
  .blog-card.alt {
    flex-direction: row-reverse;
  }
  .blog-card.alt .description:before {
    left: inherit;
    right: -10px;
  }
  .blog-card.alt .details {
    padding-left: 25px;
  }
}
